import create from 'zustand';
import { nanoid } from 'nanoid';

// Hm ly d liu t localStorage
const getLocalStorage = (key) => JSON.parse(window.localStorage.getItem(key));

// Hm lu d liu vo localStorage
const setLocalStorage = (key, value) => window.localStorage.setItem(key, JSON.stringify(value));

// To mt store  qun l trng thi ca ng dng
export const useStore = create((set) => ({
    // Texture hin ti ang s dng
    texture: 'dirt',

    // Mng cha danh sch cc khi (cubes)
    cubes: getLocalStorage('cubes') || [],

    // Thm mt khi mi vo danh sch
    addCube: (x, y, z) => {
        set((prev) => ({
            cubes: [
                ...prev.cubes,
                {
                    key: nanoid(), // To mt ID duy nht cho khi
                    pos: [x, y, z], // V tr ca khi
                    texture: prev.texture // Texture ca khi
                }
            ]
        }));
    },

    // Xa mt khi khi danh sch
    removeCube: (x, y, z) => {
        set((prev) => ({
            cubes: prev.cubes.filter(cube => {
                const [X, Y, Z] = cube.pos; // Ly ta  ca khi
                return X !== x || Y !== y || Z !== z; // Kim tra xem c phi khi cn xa khng
            })
        }));
    },

    // Thay i texture hin ti
    setTexture: (texture) => {
        set(() => ({
            texture
        }));
    },

    // Lu trng thi hin ti ca cc khi vo localStorage
    saveWorld: () => {
        set((prev) => {
            setLocalStorage('cubes', prev.cubes);
        });
    },

    // Reset trng thi ca cc khi v mc nh
    resetWorld: () => {
        set(() => ({
            cubes: []
        }));
    },
}));