import { useFrame, useThree } from "@react-three/fiber"
import { useSphere } from "@react-three/cannon"
import { useEffect, useRef } from "react"
import { Vector3 } from "three"
import { useKeyboard } from "../hooks/useKeyboard"

// Hng s cho lc nhy v tc  di chuyn
const JUMP_FORCE = 4;
const SPEED = 4;

export const Player = () => {
  // Ly trng thi bn phm (i ln, xung, tri, phi, nhy)
  const { moveBackward, moveForward, moveRight, moveLeft, jump } = useKeyboard()

  // Ly tham chiu n camera
  const { camera } = useThree()

  // To mt hnh cu vt l, i din cho nhn vt
  const [ref, api] = useSphere(() => ({
    mass: 1, // Khi lng ca vt th
    type: 'Dynamic', // Loi vt th ng
    position: [0, 1, 0], // V tr ban u
  }))

  // Lu tr vn tc v v tr hin ti ca nhn vt
  const vel = useRef([0, 0, 0])
  const pos = useRef([0, 0, 0])

  // Cp nht vn tc v v tr khi chng thay i
  useEffect(() => {
    api.velocity.subscribe((v) => vel.current = v)
  }, [api.velocity])
  useEffect(() => {
    api.position.subscribe((p) => pos.current = p)
  }, [api.position]) 

  // Cp nht cnh trong mi khung hnh
  useFrame(() => {
    // Cp nht v tr camera theo v tr nhn vt
    camera.position.copy(new Vector3(pos.current[0], pos.current[1], pos.current[2]))

    // Tnh ton hng di chuyn
    const direction = new Vector3()
    const frontVector = new Vector3(
      0, 0, (moveBackward ? 1 : 0) - (moveForward ? 1 : 0)
    )
    const sideVector = new Vector3(
      (moveLeft ? 1 : 0) - (moveRight ? 1 : 0), 0, 0
    )

    // Tnh ton vector hng di chuyn cui cng
    direction
      .subVectors(frontVector, sideVector)
      .normalize()
      .multiplyScalar(SPEED)
      .applyEuler(camera.rotation)

    // Cp nht vn tc ca nhn vt
    api.velocity.set(direction.x, vel.current[1], direction.z)

    // Kim tra nu ngi dng nhn nt nhy v nhn vt ang  trn mt t
    if (jump && Math.abs(vel.current[1]) < 0.05) {
      // p dng lc nhy ln vn tc
      api.velocity.set(vel.current[0], JUMP_FORCE, vel.current[2])
    }
  })

  return (
    <mesh ref={ref}></mesh>
  )
}