import { usePlane } from "@react-three/cannon"; // Nhp hook `usePlane`  to mt phng vt l
import { groundTexture } from "../images/textures"; // Nhp texture mt t
import { useStore } from '../hooks/useStore'; // Nhp hook  truy cp trng thi ton cc

export const Ground = () => {
  // To mt phng vt l vi hng v v tr xc nh
  const [ref] = usePlane(() => ({
    rotation: [-Math.PI / 2, 0, 0], // Xoay mt phng 90  theo trc x
    position: [0, -0.5, 0] // t mt phng  di gc ta 
  }));

  // Ly hm `addCube`  thm khi mi vo cnh
  const [addCube] = useStore((state) => [state.addCube]);

  // Lp li texture mt t 100 ln theo c chiu ngang v chiu dc
  groundTexture.repeat.set(100, 100);

  return (
    <mesh // To mt li  biu din mt phng
      onClick={(e) => {
        // Ngn s kin click lan truyn ln phn t cha
        e.stopPropagation();

        // Ly ta  im c click v lm trn
        const [x, y, z] = Object.values(e.point).map(val => Math.ceil(val));

        // Thm khi mi vo cnh ti ta   tnh
        addCube(x, y, z);
      }}
      ref={ref} // Gn tham chiu n mt phng
    >
      <planeGeometry attach='geometry' args={[100, 100]} /> // To hnh hc cho mt phng
      <meshStandardMaterial attach='material' map={groundTexture} /> // p dng texture mt t ln vt liu
    </mesh>
  );
};