import { useEffect, useState } from 'react';
import { useStore } from "../hooks/useStore";
import { useKeyboard } from "../hooks/useKeyboard";
import { dirtImg, grassImg, glassImg, logImg, woodImg } from '../images/images';

// To mt i tng cha cc hnh nh texture vi tn tng ng
const images = {
  dirt: dirtImg,
  grass: grassImg,
  glass: glassImg,
  wood: woodImg,
  log: logImg,
};

export const TextureSelector = () => {
  // Qun l trng thi hin th ca menu chn texture
  const [visible, setVisible] = useState(false);

  // Ly texture hin ti v hm  cp nht texture t store
  const [activeTexture, setTexture] = useStore((state) => [state.texture, state.setTexture]);

  // Ly trng thi cc phm s t hook useKeyboard
  const {
    dirt,
    grass,
    glass,
    wood,
    log,
  } = useKeyboard();

  // Hiu ng ph: Cp nht texture khi mt phm s c nhn
  useEffect(() => {
    // To mt i tng cha cc phm s v trng thi ca chng
    const textures = {
      dirt,
      grass,
      glass,
      wood,
      log,
    };

    // Tm phm s no ang c nhn
    const pressedTexture = Object.entries(textures).find(([k, v]) => v);

    // Nu c phm s c nhn, cp nht texture hin ti
    if (pressedTexture) {
      setTexture(pressedTexture[0]);
    }
  }, [setTexture, dirt, grass, glass, wood, log]);

  // Hiu ng ph: Hin th menu trong 2 giy khi texture c thay i
  useEffect(() => {
    // Thit lp thi gian hin th ca menu
    const visibilityTimeout = setTimeout(() => {
      setVisible(false);
    }, 2000);

    // Hin th menu
    setVisible(true);

    // Hy b timeout khi component b unmount
    return () => {
      clearTimeout(visibilityTimeout);
    };
  }, [activeTexture]);

  return visible && (
    <div className="absolute centered texture-selector">
      {/* Render cc hnh nh texture */}
      {Object.entries(images).map(([k, src]) => (
        <img
          key={k}
          src={src}
          alt={k}
          className={`${k === activeTexture ? 'active' : ''}`}
        />
      ))}
    </div>
  );
};