import { useBox } from "@react-three/cannon";
import { useState } from "react";
import { useStore } from "../hooks/useStore";
import * as textures from "../images/textures";

export const Cube = ({ position, texture }) => {
  // Qun l trng thi khi chut di qua khi
  const [isHovered, setIsHovered] = useState(false);

  // To mt khi vt l tnh ti v tr  cho
  const [ref] = useBox(() => ({
    type: 'Static',
    position
  }));

  // Ly cc hm addCube v removeCube t store ton cc  tng tc vi danh sch cc khi
  const [addCube, removeCube] = useStore((state) => [state.addCube, state.removeCube]);

  // Ly texture tng ng t th mc textures
  const activeTexture = textures[texture + 'Texture'];

  return (
    <mesh
      // X l khi chut di chuyn vo khi
      onPointerMove={(e) => {
        e.stopPropagation();
        setIsHovered(true);
      }}
      // X l khi chut di chuyn ra khi khi
      onPointerOut={(e) => {
        e.stopPropagation();
        setIsHovered(false);
      }}
      // X l khi click vo khi
      onClick={(e) => {
        e.stopPropagation();
        const clickedFace = Math.floor(e.faceIndex / 2); // Tnh ton mt no ca khi b click
        const { x, y, z } = ref.current.position; // Ly ta  ca khi

        // Nu nhn phm Alt, xa khi
        if (e.altKey) {
          removeCube(x, y, z);
          return;
        }

        // Thm khi mi  mt i din
        if (clickedFace === 0) { // Mt trc
          addCube(x + 1, y, z);
        } else if (clickedFace === 1) { // Mt sau
          addCube(x - 1, y, z);
        } else if (clickedFace === 2) { // Mt trn
          addCube(x, y + 1, z);
        } else if (clickedFace === 3) { // Mt di
          addCube(x, y - 1, z);
        } else if (clickedFace === 4) { // Mt phi
          addCube(x, y, z + 1);
        } else if (clickedFace === 5) { // Mt tri
          addCube(x, y, z - 1);
        }
      }}
      ref={ref}
    >
      <boxGeometry attach="geometry" />
      <meshStandardMaterial
        color={isHovered ? 'grey' : 'white'} // Thay i mu khi chut di qua
        map={activeTexture} // p dng texture
        transparent={true}
        opacity={texture === 'glass' ? 0.6 : 1}
        attach="material"
      />
    </mesh>
  );
};